import React from 'react'
import { NavLink } from 'react-router-dom'
import configRouter from '../untils/config/configRouter'
import '../assets/css/home.css'
import bookImg from '../assets/img/book.png'
type Props = {}

const Home = (props: Props) => {
    return (
        <div style={{ maxWidth: 1200 }} className='grid lg:grid-cols-3 sm:grid-cols-2 gap-10 my-4 mx-auto p-4'>
            <div className=' home__item__box'>
                <img className='imgThumb' src={bookImg} alt="book" />
                <div>
                    <NavLink to={configRouter.private.book} className='home__link '>S o thy chun</NavLink>
                </div>
            </div>
            <div className=' home__item__box'>
                <img className='imgThumb' src={bookImg} alt="book" />
                <NavLink to={configRouter.private.map_manager} className='home__link'>Qun l bn </NavLink>
            </div>
            <div className=' home__item__box'>
                <img className='imgThumb' src={bookImg} alt="book" />
                <NavLink to={configRouter.private.convert} className='home__link'>Chuyn i KML - CAD </NavLink>
            </div>
            <div className=' home__item__box'>
                <img className='imgThumb' src={bookImg} alt="book" />
                <NavLink to={configRouter.private.editor} className='home__link'>Chnh sa KML </NavLink>
            </div>
            <div className=' home__item__box'>
                <img className='imgThumb' src={bookImg} alt="book" />
                <NavLink to={configRouter.private.text_tool} className='home__link'>Cng c text</NavLink>
            </div>
        </div>
    )
}

export default Home